import uniRate from '../../components/uni-rate.vue'
export default {
	data() {
		return {
			id: '',
			dayId: '',
			footData: []
		}
	},
	components:{
		uniRate
	},
	onLoad(e) {
		// console.log(e)
		this.id = e.id
		this.dayId = e.dayId
		this.req()
	},
	methods: {
		req(){
			let that = this
			uni.request({
				url:this.nowUrl + '/foods/find',
				header:{
					'token':this.token
				},
				data:{
					id: this.id,
					weekId: this.dayId
				},
				success(res) {
					// console.log(res)
					that.footData = res.data.data.comments
					that.flag = true
					console.log(that.footData)
					uni.hideLoading()
				}
			})
		}
	}
}
